.search-form {
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin-top: 20px;
  margin-bottom: 40px;
  display: flex;
}

form input, button {
  width: 400px;
  height: 40px;
  border: 3px solid #20b2aa;
  border-radius: 10px;
  padding-left: 10px;
  font-size: 16px;
}

form button {
  height: 40px;
  width: 100px;
  font-size: 18px;
}

.gallery {
  grid-template-columns: repeat(5, 1fr);
  gap: 16px;
  padding-left: 10px;
  padding-right: 10px;
  display: grid;
}

.photo-card img {
  max-width: 100%;
  width: 100%;
  aspect-ratio: 16 / 9;
}

.hidden {
  visibility: hidden;
}

.info {
  display: flex;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .5);
}

.info-item {
  padding: 10px;
}

